import React from 'react';
import {Link} from 'react-router-dom';

function Header(){
    return(
        <header  style={headerStyle}>
            <h1>
                Todo List
            </h1>
            <Link style={LinkStyle} to ="/">Home</Link> | <Link style={LinkStyle} to ="/About">About</Link>
        </header>
    )
}

const LinkStyle={
    color:'#fefefe',
    textDecoration: 'none'
};

const headerStyle={
    background:'#333',
    color:'#fefefe',
    textAlign: 'center',
    padding:'10px'
}
export default Header;